// Desktop + Tablet view
@media only screen and (min-width: 640px) {
  #playerContainer {
    #companionAd {
      display: none;
      width: 298px;
      height: 248px;
      background-color: #fafafa;
      border: 1px solid #e1e1e1;
      padding: 10px;
      position: fixed;
      bottom: 68px;
      right: 0;

      &.moveAdDown {
        bottom: 0 !important;
        animation: minimizedCompanionAd 0.1s linear 1;
      }

      &.displayAd {
        display: block;
      }

      #innerAdDiv {
      }
    }

    #videoAd {
      display: none;
      position: fixed;
      right: 0px;
      bottom: 68px;
      background-color: #fafafa;
      padding: 10px;
      border: 1px solid #e1e1e1;

      &.moveAdDown {
        bottom: 0 !important;
        animation: minimizedCompanionAd 0.1s linear 1;
      }

      &.displayAd {
        display: block;
      }
    }

    &.showInteractivePlayer {
      #companionAd {
        bottom: 78px;
        animation: hideTimelineVideoAd 0.15s linear 1;
      }
      #videoAd {
        bottom: 78px;
        animation: hideTimelineVideoAd 0.15s linear 1;
      }
      #radioPlayerContainer {
        #radioPlayer {
          bottom: 10px;

          #liveButton {
            display: block;
          }

          #right {
            #timelineButton {
              display: block;
            }
          }
        }

        #timelineContainer {
          display: block;
        }
        #scrubBarContainer {
          display: block;
          visibility: visible;
          opacity: 1;
          animation: showScrubBar 0.3s linear 1;
        }
      }
    }

    &.showTimeline {
      #companionAd {
        bottom: 141px;
        animation: showTimelineVideoAd 0.3s linear 1;
      }
      #videoAd {
        bottom: 141px;
        animation: showTimelineVideoAd 0.3s linear 1;
      }
      #radioPlayerContainer {
        #timelineContainer {
          bottom: 76px;
          height: 64px;
          animation: showTimeline 0.3s linear 1;
        }
      }
    }

    #radioPlayerContainer {
      min-width: 640px;
      &.hide {
        display: none;
        visibility: hidden;
      }
      &.showA {
        #radioPlayer {
          #right {
            .dfpAdElement {
              display: block;
            }
          }
        }
      }

      &.popup {
        height: 68px;
        min-height: 68px;
        animation: showPlayer 0.3s linear 1;
      }

      &.minimized {
        width: 168px;
        height: 121px;
        min-width: 168px;

        #scrubBarContainer {
          display: none;
        }

        #timelineContainer {
          display: none;
        }

        #radioPlayer {
          bottom: 0;
          height: 121px;

          #controls {
            width: 168px;
            min-width: 168px;
            display: flex;
            justify-content: center;
            position: absolute;
            top: 51px;
          }

          #right {
            #muteButton {
              position: absolute;
              top: 7px;
              left: 8px;
            }

            #liveButton {
              display: none;
            }

            #timelineButton {
              display: none;
            }

            .toggleContainer {
              margin-left: 0px;
              position: absolute;
              top: -35px;
              right: 10px;

              #toggleMinimizeButton {
                position: relative;
                margin-left: 40px;
                margin-right: 7px;
                top: 49px;

                #minimizeIcon {
                  position: absolute;
                  left: 0px;
                  width: 19px;
                  height: 12px;
                  margin-left: 0px;
                }

                #minimizeText {
                  position: absolute;
                  right: 4px;
                }
              }
            }
          }
        }

        #minimizedStationContainer {
          bottom: 91px;
        }

        #minimizedShowContainer {
          bottom: 44px;
        }

        &.showA {
          height: 160px;

          #radioPlayer {
            height: 135px;

            #right {
              .dfpAdElement {
                position: relative;
                bottom: 24px;
                right: 134px;
                margin-right: 0px;
              }
            }

            #controls {
              position: relative;
              top: 35px;
            }
          }
          #minimizedStationContainer {
            bottom: 70px;
          }
          #minimizedShowContainer {
            bottom: 21px;
          }
        }
      }
      #timelineContainer {
        width: 100%;
        height: 0;
        bottom: 0px;
        position: absolute;

        &.hideTimeline {
          animation: hideTimeline 0.4s linear 1;
        }

        #timelineNextButton {
          position: absolute;
          z-index: 10;
          width: 28px;
          height: 63px;
          top: 0;
          right: 0;
          opacity: 0;

          #timelineNextIcon {
            background: url(data:image/svg+xml;base64,)
              no-repeat left center;
            background-size: contain;
            transform: rotate(90deg);
            width: 21px;
            height: 39px;
            right: 3px;
            position: absolute;
            top: 15px;
          }
        }

        #timelinePrevButton {
          position: absolute;
          z-index: 10;
          width: 28px;
          height: 63px;
          opacity: 0;

          #timelinePrevIcon {
            background: url(data:image/svg+xml;base64,)
              no-repeat left center;
            background-size: contain;
            transform: rotate(-90deg);
            width: 21px;
            height: 39px;
            left: 3px;
            position: absolute;
            top: 15px;
          }
        }

        //Replayable feature

        .timelineShow.nonReplayableShow {
          pointer-events: none;
          border: none;
          .timelineShowDetails {
            border: 2px solid;
            border-bottom: 3px solid;
            border-color: #979797;
            background: #979797;
          }
        }

        .timelineShow {
          display: inline-block;
          border: solid 1px #e1e1e1;
          vertical-align: top;

          .timelineShowStartTime {
            height: 16px;
            font-family: CircularStd;
            font-size: 10px;
            padding-top: 4px;
            padding-left: 4px;
            background: #fff;
            color: #2d2d2d;
          }

          .timelineShowDetails {
            background-color: #eeeeee;
            height: 42px;
            display: flex;

            .timelineShowButtonContainer {
              &.loading {
                top: 7px;
                left: 7px;

                .timelineShowButton {
                  background: url(data:image/svg+xml;base64,)
                    no-repeat left center;
                  background-size: contain;
                  width: 24px;
                  height: 24px;
                  top: -7px;
                  left: -6px;
                  position: relative;
                }
              }
              .timelineLoadingSpinner {
                width: 30px;
                height: 30px;
              }
              .timelineShowButton {
                width: 24px;
                height: 24px;
                min-width: 24px;
                min-height: 24px;
                cursor: pointer;
                background: url(data:image/svg+xml;base64,)
                  no-repeat left center;
                background-size: contain;
                margin-top: 9px;
                margin-left: 9px;
              }
            }

            .timelineShowNameTime {
              display: flex;
              flex-direction: column;
              overflow: hidden;

              .timelineShowName {
                font-family: CircularStd;
                font-size: 16px;
                font-weight: bold;
                color: #1b1b1b;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                margin-top: 1px;
                margin-left: 9px;
              }

              .timelineShowTime,
              .timelineShowTimeLive {
                margin-top: 5px;
                margin-left: 9px;
                opacity: 0.84;
                font-family: CircularStd;
                font-size: 10px;
                font-weight: 500;
                color: #1b1b1b;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
              }

              .timelineShowTimeLive {
                display: flex;

                .timelineShowLiveIndicator {
                  width: 5px;
                  height: 5px;
                  border-radius: 3px;
                  margin-top: 4px;
                  margin-right: 4px;
                  margin-left: 1px;
                }
              }
            }
          }

          &.timelineCurrentShow,
          &.timelineLiveShow,
          &.timelinePreviousShow {
            cursor: pointer;

            &:hover {
              .timelineShowDetails {
                background-color: #f5f5f5;
              }
            }
          }

          &.timelineUpcomingShow {
            cursor: default;
            border:none;
            // .timelineShowDetails {
              
            // }
           
            .timelineShowDetails {
              background-color: #979797;
              border: 2px solid;
              border-bottom: 3px solid;
              border-color: #979797;
              .timelineShowNameTime {
                .timelineShowName {
                  color: #ffffff;
                  margin-left: 7px;
                }

                .timelineShowTime {
                  color: #ffffff;
                  margin-top: 4px;
                }
              }
            }
          }
        }

        .tns-nav,
        .tns-controls {
          display: none;
        }
      }
      #scrubBarContainer {
        display: block;
        position: relative;
        visibility: hidden;
        opacity: 0;
        animation: hideScrubBar 0.2s linear 1;

        #scrubBar {
          height: 10px;
          width: 100%;
          cursor: pointer;
          position: relative;
          z-index: 1;
          bottom: 10px;
        }

        .rangeSlider {
          bottom: 10px;
          height: 10px;
          border-radius: 0;
          box-shadow: inset 0 0 4px 4px #f5f5f5;
          background-color: #ffffff;
          border: solid 0.5px #e1e1e1;
          border-left: none;
          border-right: none;

          .rangeSlider__fill {
            border-radius: 0;
            box-shadow: none;
            background-color: #ff4940;
            top: -1px;
            height: calc(100% + 2px);
          }

          .rangeSlider__handle {
            width: 14px;
            height: 14px;
            background-color: #ffffff;
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
            top: -3px;

            &::after {
              content: none;
            }
          }

          .rangeSlider__buffer {
            height: 10px;
            border-radius: 5px;
            background-color: #cbcbcb;
            top: 0;
          }
        }

        #dialogBox {
          border: #e1e1e1 0.5px solid;
          background-color: #fff;
          left: -18px;
          width: 52px;
          height: 20px;
          position: absolute;
          display: inline-block;
          box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.25);
          z-index: 5;
          bottom: 28px;
          display: none;
          padding-top: 4px;

          #liveCircle {
            width: 6px;
            height: 6px;
            background-color: #ff4940;
            border-radius: 4px;
            display: inline-block;
            position: absolute;
            top: 9px;
            left: 8px;
          }

          #liveText {
            font-family: CircularStd;
            font-size: 12px;
            font-weight: 900;
            color: #1f055e;
            position: relative;
            top: 1px;
            left: 20px;
          }

          #dialogPoint {
            width: 0;
            height: 0;
            border: 5px solid;
            border-color: #fff transparent transparent;
            position: absolute;
            top: 18px;
            left: 21px;
          }
        }

        #cuePointContainer {
          .cuePoint {
            position: absolute;
            background-color: #ffffff;
            width: 4px;
            height: 10px;
            z-index: 3;
            bottom: 10px;
            cursor: pointer;
          }
          .cuePointIndicator {
            border: #e1e1e1 0.5px solid;
            background-color: #fff;
            text-transform: capitalize;
            left: -18px;
            padding: 3px 3px 7px 3px;
            position: absolute;
            display: none;
            box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.25);
            z-index: 5;
            bottom: 28px;
            border-radius: 2px;
            .cuePointIndicatorText {
              font-family: CircularStd;
              font-size: 12px;
              font-weight: 900;
              color: #1f055e;
              position: relative;
              top: 2px;
              text-align: center;
              white-space: nowrap;
            }
            .cuePointIndicatorPoint {
              width: 0;
              height: 0;
              border: 5px solid;
              border-color: #fff transparent transparent;
              position: absolute;
              bottom: -9px;
              left: 30px;
            }
          }
        }
      }

      .radioItem {
        display: flex;
        align-items: center;
      }

      #radioPlayer {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        position: relative;
        height: 68px;
        bottom: 10px;
      }

      #station {
        flex: 1;

        #poster {
          display: inline-block;
          width: 66px;
          height: 66px;
          min-width: 66px;
          min-height: 66px;
        }

        #info {
          margin-left: 12px;
          margin-top: -5px;

          #stationNameContainer {
            width: 300px;
            display: flex;
            align-items: center;

            #infoOval {
              margin-right: 5px;
            }

            #stationNameWrapper {
              max-width: 287px;

              #stationName {
              }
            }
          }

          #showNameContainer {
            #showNameWrapper {
              width: 300px;
              #showName {
              }
            }
          }

          #showDescriptionContainer {
            font-size: 12px;
            margin-top: 3px;
            min-height: 14px;

            #showDescriptionWrapper {
              width: 300px;
            }
          }

          #titleArtistContainer {
            display: none;
          }
        }
      }

      #controls {
        justify-content: center;
        vertical-align: top;
        flex: 1;

        div {
        }

        #playButton {
          height: 36px;
          width: 36px;

          &.stop {
          }
        }

        #prevButton {
          height: 18px;
          width: 18px;
          margin-right: 13px;

          &.disabled {
          }
        }

        #nextButton {
          height: 18px;
          width: 18px;
          margin-left: 12px;

          &.disabled {
          }
        }

        #rewindButton {
          height: 18px;
          width: 18px;
          margin-right: 12px;
        }

        #forwardButton {
          height: 18px;
          width: 18px;
          margin-left: 12px;
        }

        #player {
        }
      }

      .mejs__container {
      }

      #right {
        flex: 1;
        justify-content: flex-end;

        #volumeControl {
          position: relative;
          right: -13px;
        }

        #timelineButton {
          background: url(data:image/svg+xml;base64,)
            no-repeat left center;
          background-size: contain;
          height: 16px;
          width: 16px;
          margin-left: 13px;
          cursor: pointer;
          display: none;
          opacity: 1;

          &:hover {
            opacity: $hoverOpacity;
          }
        }
        #liveButton {
          width: 52px;
          height: 20px;
          border-radius: 2px;
          background-color: #eee;
          border: solid 1px #ffffff;
          cursor: pointer;
          display: none;

          #liveButtonCircle {
            width: 6px;
            height: 6px;
            border-radius: 4px;
            position: relative;
            top: 7px;
            left: 10px;
          }

          #liveButtonText {
            font-family: CircularStd;
            font-size: 12px;
            font-weight: 500;
            color: #1b1b1b;
            position: relative;
            top: -3px;
            left: 19px;
          }

          &.inactive {
            opacity: 0.57;

            #liveButtonCircle {
              background-color: #ffffff;
            }
          }
        }

        #muteButton {
          display: inline-block;
          height: 15px;
          width: 15px;
          min-width: 15px;
          padding-right: 6px;
          position: relative;
          right: -10px;

          &.muted {
          }
        }

        #toggleMinimizeButton {
          top: 50px;
          right: 8px;
          display: flex;
          flex-direction: row;
          align-items: center;
          cursor: pointer;
          position: absolute;

          #minimizeIcon {
            height: 12px;
            width: 18px;
            padding-right: 5px;
            margin-bottom: 2px;
          }

          #minimizeText {
            font-size: 12px;
          }
        }

        .toggleContainer {
          margin-left: 20px;

          .toggleItem {
            height: 20%;
          }
        }

        .dfpAdElement {
          width: 100px;
          min-width: 100px;
          height: 35px;
          margin-right: 10px;
        }
      }

      #minimizedShowContainer {
        margin-left: 27px;
        margin-right: 27px;
        position: relative;

        #minimizedShowWrapper {
          #minimizedShow {
            font-size: 11px;
          }
        }
      }

      #minimizedStationContainer {
        margin-left: 27px;
        margin-right: 27px;
        position: relative;

        #oval {
          width: 6px;
          height: 6px;
        }

        #minimizedStationWrapper {
          #minimizedStation {
            font-size: 11px;
          }
        }
      }
    }
  }
}
