.carousel {
  display: flex;
  white-space: nowrap;
  width: max-content;
  align-items: flex-start;
  margin-left: -0.625rem;
}

.carousel-container {
  position: relative;
  height: 21rem;
  margin-bottom: 2.875rem;
  opacity: 0.6;
  position: relative;

  &:focus-within {
    opacity: 1;
  }
}

.virtualized {
  overflow: hidden !important;

  & > div {
    padding-right: 1200px;
  }
}

.column {
  margin: 0.625rem;
}
