.keypad {
    height: 25rem;
    min-width: 25rem;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    background-color: #0a021f;

    &:focus-within {
        background-color: #130539;
    }
}

.keypad-static {
    width: 23.13rem;
    max-width: 23.13rem;
    min-width: 23.13rem;
    height: 24.38rem;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    background-color: #0a021f;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    &:focus-within {
        background-color: #130539;
    }

    .key {
        width: 3.855rem;
        height: 3.483rem;
    }

    .bottom-keys {
        width: 5.78rem;
    }
}

.key {
    opacity: 0.3;
    width: 100%;
    font-family: 'Circular Medium';
    font-size: 1.75rem;
    font-weight: 500;
    background: none;
    border: 0;
    overflow: visible;
    padding: 0;
    -webkit-appearance: button;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    color: #eee;

    &:focus,
    .highlight {
        opacity: 1;
        background-color: #eeeeee;
        color: #1e055f;
        outline: none;
    }

    &.keypad {
        background-color: #130539;
    }
}

.search-row {
    height: 14%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
