.subnav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 5.75rem;
}

.search-container {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 3rem;

  & .search-results:focus-within {
    &+.keypad-static {
      background-color: #130539;
    }
  }
}


.no-result-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.magnifying-glass {
  margin: 1.813rem auto 0 auto;
}

.search-results {
  color: #eee;
  font-family: 'CircularStd';
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 0.3px;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  flex-grow: 1;

  .carousel-container {
    padding-left: 1.275rem;
  }

  span {
    margin-left: 1.875rem;
  }

  &:focus-within {
    opacity: 1;
  }
}