[type='range'] {
  --sx: calc(var(--val) * 1%);
  margin: 0;
  padding: 0;
  width: 78px;
  height: 14px;
  background: transparent;
  font: 1em/1 arial, sans-serif;
  outline-width: 0; }
  [type='range'], [type='range']::-webkit-slider-thumb {
    -webkit-appearance: none; }
  [type='range']::-webkit-slider-runnable-track {
    border-radius: 3px;
    box-sizing: border-box;
    border: none;
    width: 78px;
    height: 8px;
    background: #fafafa;
    color: transparent; }
  [type='range']::-moz-range-track {
    border-radius: 3px;
    box-sizing: border-box;
    border: none;
    width: 78px;
    height: 8px;
    background: #fafafa;
    color: transparent; }
  [type='range']::-ms-track {
    border-radius: 3px;
    box-sizing: border-box;
    border: none;
    width: 78px;
    height: 8px;
    background: #fafafa;
    color: transparent; }
  [type='range']::-webkit-slider-thumb {
    margin-top: -3px;
    box-sizing: border-box;
    border: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: url(data:image/svg+xml;base64,) no-repeat left center;
    background-size: contain; }
  [type='range']::-moz-range-thumb {
    box-sizing: border-box;
    border: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: url(data:image/svg+xml;base64,) no-repeat left center;
    background-size: contain; }
  [type='range']::-ms-thumb {
    margin-top: 0;
    box-sizing: border-box;
    border: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: url(data:image/svg+xml;base64,) no-repeat left center;
    background-size: contain; }
  [type='range']::-ms-tooltip {
    display: none; }
  [type='range']::-ms-ticks-before {
    color: transparent; }
  [type='range']::-ms-ticks-after {
    color: transparent; }

