// Breakpoints
$bp-720p: 1280px;
$bp-1080p: 1920px;
$bp-1440p: 2560px;
$bp-4k: 3840px;

// Media Queries
$mq-small: '(min-width: #{$bp-720p})';
$mq-medium: '(min-width: #{$bp-1080p})';
$mq-large: '(min-width: #{$bp-1440p})';
$mq-xlarge: '(min-width: #{$bp-4k})';

// Font-Size
$base-font-size: 16px;

// Line-Height
$base-line-height: 1.5;

html {
  font-size: $base-font-size;
  line-height: $base-line-height;

  @media #{$mq-medium} {
    font-size: $base-font-size * 1.5;
    line-height: $base-line-height * 1.2;
  }

  @media #{$mq-large} {
    font-size: $base-font-size * 2;
  }

  @media #{$mq-xlarge} {
    font-size: $base-font-size * 3;
  }
}

body {
  margin: 0;
  padding: 0;

  font-family: 'CircularStd', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a021f;
  box-sizing: border-box;
}

span,
button,
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
ul,
a {
  letter-spacing: 0.008rem;
}
.homeContainer {
  text-align: center;
}

.fullScreenContainer {
  height: 100vh;
  width: 100vw;
}

.sizefillcontainer {
  height: 100%;
  width: 100%;
  margin: auto;
}

.centeredContentContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

