.nav-container {
  display: flex;
  position: fixed;
  top: -1px;
  background: #0a021f;
  width: 100%;
  padding-top: 4rem;
  // min-height: 9.375rem;
  z-index: 100;
  left: 0;
  flex-direction: column;
  justify-content: flex-start;
}

.nav-hidden {
  opacity: 0;
}

.nav-blur {
  opacity: 0.4;

  .focushere {
    outline: none;
    transform: scale(1.1);
    border-bottom: 2px solid #EEEEEE;
  }
}