code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Subsetted */

@font-face {
  font-family: 'Circular Black';
  src: url('./fonts/lineto-circular-black.eot');
  src: url('./fonts/lineto-circular-black.eot?#iefix')
      format('embedded-opentype'),
    url('./fonts/lineto-circular-black.woff2') format('woff2'),
    url('./fonts/lineto-circular-black.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Circular Black Italic';
  src: url('./fonts/lineto-circular-blackitalic.eot');
  src: url('./fonts/lineto-circular-blackitalic.eot?#iefix')
      format('embedded-opentype'),
    url('./fonts/lineto-circular-blackitalic.woff2') format('woff2'),
    url('./fonts/lineto-circular-blackitalic.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Circular Bold';
  src: url('./fonts/CircularStd-Bold.eot'); /* IE9 Compat Modes */
  src: url('./fonts/CircularStd-Bold.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('./fonts/CircularStd-Bold.otf') format('opentype'),
    /* Open Type Font */ url('./fonts/CircularStd-Bold.svg') format('svg'),
    /* Legacy iOS */ url('./fonts/CircularStd-Bold.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('./fonts/CircularStd-Bold.woff')
      format('woff'),
    /* Modern Browsers */ url('./fonts/CircularStd-Bold.woff2') format('woff2'); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Circular Bold';
