@font-face {
  font-family: 'CircularStd';
  src: url('../fonts/circular-std/circularstd-book-webfont.eot'),
    url('../fonts/circular-std/circularstd-book-webfont.woff2') format('woff2'),
    url('../fonts/circular-std/circularstd-book-webfont.woff') format('woff'),
    url('../fonts/circular-std/circularstd-book-webfont.ttf') format('truetype');
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}

@keyframes showPlayer {
  0% {
    height: 0;
    min-height: 0;
  }
  100% {
    height: 78px;
    min-height: 68px;
  }
}

@keyframes showPlayerMobile {
  0% {
    height: 0;
  }
  100% {
    height: 136px;
  }
}

@keyframes showPlayerMobileAd {
  0% {
    height: 0;
  }
  100% {
    height: 166px;
  }
}

@keyframes minimizedCompanionAd {
  0% {
    bottom: 78px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes showTimeline {
  0% {
    height: 0;
  }
  100% {
    height: 64px;
  }
}

@keyframes hideTimeline {
  0% {
    height: 64px;
    bottom: 77px;
  }
  100% {
    height: 0px;
    bottom: 0px;
  }
}

@keyframes showTimelineVideoAd {
  0% {
    bottom: 78px;
  }
  100% {
    bottom: 141px;
  }
}

@keyframes hideTimelineVideoAd {
  0% {
    bottom: 141px;
  }
  100% {
    bottom: 78px;
  }
}

@keyframes showScrubBar {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes hideScrubBar {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes loadingSpinner {
  100% {
    transform: rotate(360deg);
  }
}

#playerContainer {
  #companionAd {
    display: none;
  }

  #videoAd {
    display: none;
  }
  &.showInteractivePlayer {
    #radioPlayerContainer {
      #radioPlayer {
        #controls {
          #prevButton,
          #nextButton,
          #rewindButton,
          #forwardButton {
            display: block;
          }
        }
      }
    }
  }

  #radioPlayerContainer {
    #timelineContainer {
      border-bottom: 2px solid #eeeeee;
      .timelineShow {
        .timelineShowDetails {
          .timelineShowButtonContainer {
            position: relative;
            width: 36px;
            height: 36px;
            min-width: 36px;
            min-height: 36px;

           .disabled {
            opacity: $disabled;
          }

            &.loading {
              .timelineLoadingSpinner {
                display: block;
                animation: loadingSpinner 1s infinite linear;
              }

              .timelineShowButton {
                opacity: $disabled !important;
                cursor: default;

                &:hover {
                  opacity: $disabled !important;
                }
              }
            }

            .timelineLoadingSpinner {
              background: url(data:image/svg+xml;base64,)
                no-repeat left center;
              background-size: contain;
              width: 36px;
              height: 36px;
              display: none;
              position: absolute;
              top: -1px;
            }
          }
        }
        &.timelineCurrentShow {
          border: 1px solid;
        }
      }
    }

    #radioPlayer {
      #controls {
        #playButtonContainer {
          position: relative;
          width: 36px;
          height: 36px;

          &.loading {
            #loadingSpinner {
              display: block;
              animation: loadingSpinner 1s infinite linear;
            }

            #playButton,
            #playButton.stop {
              background: url(data:image/svg+xml;base64,)
                no-repeat left center;
              background-size: contain;
              width: 36px;
              height: 36px;
              position: absolute;
              top: -1px;
              left: 0px;
            }
          }

          #loadingSpinner {
            background: url(data:image/svg+xml;base64,)
              no-repeat left center;
            background-size: contain;
            width: 36px;
            height: 36px;
            display: none;
            position: absolute;
            top: -1px;
          }
        }
      }
    }
  }
}

#radioPlayerContainer {
  font-family: CircularStd;
  color: white;
  position: fixed;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
  background: #1f055e;
  left: 0;
  height: 0;
  -webkit-transition: width 0.2s, min-width 0.2s, height 0.2s;
  transition: width 0.2s, min-width 0.2s, height 0.2s;

  &.minimized {
    #radioPlayer {
      #controls {
        #playButton {
          position: relative;
          top: 5px;
        }
      }

      #right {
        #muteButton {
        }

        .toggleContainer {
          #toggleMinimizeButton {
            #minimizeIcon {
              background: url(data:image/svg+xml;base64,)
                no-repeat left center;
              background-size: contain;
            }

            #minimizeText {
            }
          }
        }
      }
    }
  }

  #scrubBarContainer {
    display: none;

    .rangeSlider--disabled {
      opacity: 1;
    }
  }

  #timelineContainer {
    display: none;
  }

  .radioItem {
  }

  .hide {
    display: none !important;
  }

  #radioPlayer {
    background-color: #1f055e;

    #right {
      .dfpAdElement {
        display: none;
      }
    }
  }

  #station {
    background: #1f055e;

    #poster {
      background: black;
      background-size: contain;
      border: solid 1px #1f055e;
      border-radius: 3px;
      background-repeat: no-repeat;
    }

    #info {
      #stationNameContainer {
        overflow: hidden;
        white-space: nowrap;

        &.marquee {
          #stationNameWrapper {
            overflow: hidden;

            #stationName {
              animation: marquee 8s linear infinite;
              padding-left: 100%;
              display: inline-block;
            }
          }
        }

        #infoOval {
          width: 8px;
          height: 8px;
          background-color: #ff4940;
          border-radius: 4px;
          display: inline-block;
        }

        #stationName {
          display: inline-block;
          opacity: 0.87;
          font-family: CircularStd;
          font-size: 12px;
        }
      }

      #showNameContainer {
        display: block;
        white-space: nowrap;
        font-family: CircularStd;
        font-size: 16px;
        margin-top: 2px;
        overflow: hidden;

        #showNameWrapper {
          margin: auto;

          #showName {
            display: inline-block;
          }
        }

        &.marquee {
          #showNameWrapper {
            overflow: hidden;
            #showName {
              animation: marquee 8s linear infinite;
              padding-left: 100%;
              display: inline-block;
            }
          }
        }
      }

      #showDescriptionContainer {
        display: block;
        white-space: nowrap;
        font-family: CircularStd;
        font-size: 16px;
        margin-top: 2px;
        overflow: hidden;

        #showDescriptionWrapper {
          margin: auto;

          #showDescription {
            display: inline-block;
          }
        }

        &.marquee {
          #showDescriptionWrapper {
            overflow: hidden;
            #showDescription {
              animation: marquee 8s linear infinite;
              padding-left: 100%;
              display: inline-block;
            }
          }
        }
      }

      #titleArtistContainer {
        display: block;
        white-space: nowrap;
        font-family: CircularStd;
        font-size: 16px;
        margin-top: 2px;
        overflow: hidden;

        #titleArtistWrapper {
          margin: auto;

          #titleArtist {
            display: inline-block;
          }
        }

        &.marquee {
          #titleArtistWrapper {
            overflow: hidden;
            #titleArtist {
              animation: marquee 8s linear infinite;
              padding-left: 100%;
              display: inline-block;
            }
          }
        }
      }
    }
  }

  #controls {
    div {
      background-position: center;
    }

    #playButton {
      display: inline-block;
      cursor: pointer;
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      background-size: contain;

      &.stop {
        background: url(data:image/svg+xml;base64,)
          no-repeat left center;
        background-size: contain;
      }

      &.disabled {
        opacity: $disabled !important;
        cursor: default;

        &:hover {
          opacity: $disabled !important;
        }
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #prevButton {
      display: none;
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      cursor: pointer;
      background-size: contain;

      &.disabled {
        opacity: $disabled !important;
        cursor: default;

        &:hover {
          opacity: $disabled !important;
        }
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #nextButton {
      display: none;
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      cursor: pointer;
      background-size: contain;

      &.disabled {
        opacity: $disabled !important;
        cursor: default;

        &:hover {
          opacity: $disabled !important;
        }
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #rewindButton {
      display: none;
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      cursor: pointer;
      background-size: contain;

      &.disabled {
        opacity: $disabled !important;
        cursor: default;

        &:hover {
          opacity: $disabled !important;
        }
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #forwardButton {
      display: none;
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      cursor: pointer;
      background-size: contain;

      &.disabled {
        opacity: $disabled !important;
        cursor: default;

        &:hover {
          opacity: $disabled !important;
        }
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #player {
      display: none;
    }
  }

  .mejs__container {
    width: 100% !important;
    display: none;
  }

  #right {
    #volumeControl {
      cursor: pointer;
    }

    #muteButton {
      background: url(data:image/svg+xml;base64,)
        no-repeat left center;
      cursor: pointer;

      &.muted {
        background: url(data:image/svg+xml;base64,)
          no-repeat left center;
      }

      &:hover {
        opacity: $hoverOpacity;
      }
    }

    #toggleMinimizeButton {
      &:hover {
        opacity: $hoverOpacity;
      }

      #minimizeIcon {
        background: url(data:image/svg+xml;base64,)
          no-repeat left center;
        background-size: contain;
      }

      #minimizeText {
        font-family: 'CircularStd';
      }
    }

    .toggleContainer {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .toggleItem {
      }
    }
  }

  #minimizedShowContainer {
    white-space: nowrap;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    &.marquee {
      #minimizedShowWrapper {
        width: 114px;

        #minimizedShow {
          padding-left: 100%;
          animation: marquee 8s linear infinite;
        }
      }
    }

    #minimizedShowWrapper {
      overflow: hidden;
      max-width: 114px;

      #minimizedShow {
        display: inline-block;
      }
    }
  }

  #minimizedStationContainer {
    white-space: nowrap;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    &.marquee {
      #minimizedStationWrapper {
        width: 100px;

        #minimizedStation {
          padding-left: 100%;
          animation: marquee 8s linear infinite;
        }
      }
    }

    #oval {
      border-radius: 5px;
      margin-right: 5px;
      background-color: #ff4940;
    }

    #minimizedStationWrapper {
      overflow: hidden;
      max-width: 100px;

      #minimizedStation {
        display: inline-block;
      }
    }
  }
}

#overlayPlayerContainer {
  position: fixed;
  right: 84px;
  top: 40px;
  z-index: 101;

  width: 278px;
  height: 60px;
  box-shadow: 0 2px 9px 0 rgba(0, 0, 0, 0.5);
  background: #1e055f;
  display: flex;
  padding: 12px;

  &.hide {
    display: none;
  }

  &:active {
    background-image: linear-gradient(to bottom, #3c01b7, #1e055f);
  }

  #overlayInfo {
    margin-left: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    #overlayPlayerStatus {
      font-family: CircularStd;
      font-size: 14px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.1px;
      color: #ff4940;
    }

    #overlayStationName {
      opacity: 0.8;
      font-family: CircularStd;
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.1px;
      color: #eeeeee;
    }

    #overlayShowName {
      font-family: CircularStd;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.1px;
      color: #eeeeee;
      width: 204px;
      height: 19px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  #overlayPoster {
    display: inline-block;
    width: 60px;
    height: 60px;
    background: black;
    background-size: contain;
    border: solid 1px #1f055e;
    border-radius: 3px;
    background-repeat: no-repeat;
    position: relative;

    &.inactive::after {
      content: '';
      display: inline-block;
      width: 23px;
      height: 30px;
      position: absolute;
      top: 16px;
      left: 22px;
      background-image: url(data:image/svg+xml;base64,);
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  #overlayStationName {
    color: white;
    font-family: CircularStd;
    font-size: 16px;
    font-weight: 500;
  }
}
